<template>
    <div class="list-con" >
        <div v-for="(item) of list" :key="item.goodsId">
            <div class="list-li">
                <router-link :to="{name:'Payment',query:{goodsId: item.goodsId,goodsName: item.goodsName,price: item.price,urlp: item.picture}}">
                    <img :src=url+item.picture alt="" class="list-img">
                </router-link>
                <h2 class="list-title">{{item.goodsName}}</h2>
                <li class="information">
                    <p class="price">￥{{item.price}}</p>
                    <p class="number">月销 8085笔</p>
                </li>
                <li class="list-btn">
                    <el-button class="buy" size="small">
                        <router-link :to="{path:'/Payment',query:{goodsId: item.goodsId,goodsName: item.goodsName,price:item.price,urlp: item.picture}}" tag="p">点击购买</router-link>
                    </el-button>
                    <p class="iconfont cart" @click="showModal(item)">&#xe61c;</p>
                </li>
            </div>
        </div>
        <div class="modal" v-show="show">
            <p>成功加入购物车</p>
        </div>
    </div>
</template>

<script>
export default {
  name: 'List',
  data () {
    return {
      list: [],
      show: false,
      timeout: 1000,
      url: 'http://192.168.31.6:8890',
      listArray: []
    }
  },
  methods: {
    showModal (e) {
      this.show = true
      setTimeout(() => {
        this.show = false
      }, this.timeout)
      let listArray = this.listArray
      listArray.push(e)
      console.log(listArray)
      localStorage.setItem('goodsList', JSON.stringify(listArray))
    }
  },
  mounted () {
    this.axios.post('/api/goods/listData/').then(res => {
      console.log(res.data)
      this.list = res.data.list
    }, res => {
      console.info('调用失败')
    })
  }
}
</script>

<style lang="stylus" scoped>
@import "~styles/varibles.styl"
@import "~styles/mixins.styl"
.list-con
    display :flex
    flex-wrap :wrap
    justify-content space-between
    background-color:#eee
    padding-bottom 1.4rem
    position relative
    .list-li
        width :42.5vw
        margin 8px 6px 0 6px
        background-color #ffffff
        border-radius 5px
        padding .12rem
        .list-img
            width: 100%
        .list-title
            font-size : .36rem
            margin .2rem 0
            ellipsis()
        .information
            display: flex
            justify-content space-between
            margin .3rem 0
            .price
                color rgba(212, 48, 48, 1)
                font-size .32rem
            .number
                color rgba(102, 102, 102, 1)
                font-size .28rem
        .list-btn
            display: flex
            justify-content space-between
            .buy
                background-color $bgColor
                color #fff
                width 70%
                p{
                    color #ffffff
                }
            .cart
                font-size .5rem
                color rgba(153, 153, 153, 1)
                margin-right .15rem
    .modal
        position absolute
        top 40%
        left 54%
        margin-top -100px
        margin-left -100px
        opacity .8
        background-color #363636
        padding .3rem .6rem
        border-radius 5px
        p
            color #ffffff
            text-align center
            font-size .3rem
</style>



